<template>
  <div id="FindTheNumber" class="pb-16">
    
    <div v-show="alert.status" :class="alert.class" class="fixed z-50 w-full flex justify-center alertFade" style="top:10%">
      <span :class="alert.color" class="px-10 py-2 rounded-lg font-bold text-black text-3xl" v-text="alert.msg" />
    </div>
    
    <div class="w-full flex flex-col items-center mt-16 font-bold text-sm lg:text-xl">
      <div class="w-full flex flex-wrap justify-center max-w-xl items-center">
        <div class="">
          <button class='bg-purple-500 uppercase rounded-lg py-2 px-4 leading-tight focus:outline-none cursor-pointer' @click="changeSettings()">restart</button>
        </div>
      </div>
      <div class="w-full flex flex-wrap justify-between max-w-xl mt-5 items-center">
        <div class="ml-10">Score : <span v-text="score" /></div>
        <div class="mr-10">Time Left : <span :class="getTimeColor()" v-text="timer"/> s</div>
      </div>
    </div>

    <div class="w-full flex flex-col items-center font-bold">
      <div class="w-full flex flex-col max-w-xl mt-3 px-5 text-2xl">

        <div class="h-full w-full py-12 rounded-lg overflow-hidden border bg-purple-400" v-text="question" />
        <br>
        <div class="h-full w-full p-1 rounded-lg overflow-hidden border bg-purple-400" v-text="userNumber?userNumber:'.'"/>
        <br>
        <div class="w-full flex flex-wrap">
          
            <div class="w-4/5 flex flex-wrap">
              <div class="w-1/3 flex flex-wrap" v-for="(number, index) in 9" :key="index">
                <div class="w-full py-3 m-1 rounded-lg overflow-hidden cursor-pointer border bg-purple-500" v-text="number" @click="clickNumber(number)"/>
              </div>
            </div>
            <div class="w-1/5 flex flex-col">
              <div class="w-full py-8 m-1 rounded-lg overflow-hidden cursor-pointer border bg-purple-400" v-text="'clear'" @click="clickClear()"/>
              <div class="w-full py-8 m-1 rounded-lg overflow-hidden cursor-pointer border bg-purple-500" v-text="'0'" @click="clickNumber(0)"/>
            </div>

        </div>
      </div>
    </div>
    
    <div class="w-full flex mt-10 justify-center">
      <div class="w-full flex flex-wrap justify-center max-w-5xl">
        <div class='w-1/3 px-3 mb-6'>
          <label class='block uppercase tracking-wide text-sm font-bold mb-2'>Levels:</label>
          <div class='relative'>
            <select v-model="level" class='block appearance-none w-full bg-purple-300 text-gray-700 py-3 px-4 pr-8 rounded leading-tight focus:outline-none'>
              <option v-for="(level,index) in levels" :value="index" :key="level">{{ level }}</option>
            </select>
            <div class='pointer-events-none absolute inset-y-0 right-0 flex items-center px-2 text-gray-700'>
            <svg class='fill-current h-4 w-4' xmlns='http://www.w3.org/2000/svg' viewBox='0 0 20 20'>
            <path d='M9.293 12.95l.707.707L15.657 8l-1.414-1.414L10 10.828 5.757 6.586 4.343 8z'/>
            </svg>
            </div>
          </div>
        </div>
      </div>
    </div>
    
  </div>
</template>

<script>
export default {
  name: 'FindTheNumber',
  components: {
  },
  data() {
    return {
      question:"20 - 6",
      answer:550,
      mathEx:["+","-","*","/"],

      userNumber:0,

      score:0,

      timeLimit: 60,
      timePassed: 0,
      timerInterval: null,

      stopGame:false,


      levels:["Easy","Medium","Hard"],
      level:0,
      
      alert:{status:false,class:"opacity-0",color:null,msg:null},
      alerttimeout1:null,
      alerttimeout2:null,
    }
  },
  created() {
    this.MStart();
  },
  methods: {
    MStart(){
      this.getQuestion();
    },
    clickNumber(num){
      var vm=this;
      if(vm.stopGame)return;
      if(!vm.timerInterval) vm.startTimer();
      if(vm.userNumber > 1000) return;

      vm.userNumber *= 10
      vm.userNumber += num;

      vm.check();
    },
    check(){
      var vm=this;
      if(vm.userNumber.toLocaleString().length >= vm.answer.toLocaleString().length){
        if(vm.userNumber == vm.answer){
          vm.score += 2;
          vm.alertShow("+2")
          console.log("+2")
        }else {
          if(vm.score > 0)vm.score -= 1;
          if(vm.score > 0)vm.alertShow("-1","bg-red-500")
          else vm.alertShow("0","bg-red-500")
          console.log("-1")
        }

        this.userNumber = 0;
        this.getQuestion();
      }
    },
    clickClear(){
      this.userNumber = 0
    },
    getQuestion(){
      var vm=this;
      var num1=0,num2=0,questionPR,answerPR;
      var mathE = vm.mathEx[Math.floor(Math.random() * vm.mathEx.length)];

      if (mathE == "+") {
        num1 = Math.floor(Math.random() * vm.getRandomRange());
        num2 = Math.floor(Math.random() * vm.getRandomRange());
        
        questionPR = num1+" + "+num2
        answerPR = num1 + num2
      }else if (mathE == "-") {
        do {
          num1 = Math.floor(Math.random() * vm.getRandomRange());
          num2 = Math.floor(Math.random() * vm.getRandomRange());
        } while (num1 < num2);
        
        questionPR = num1+" - "+num2
        answerPR = num1 - num2
      }else if (mathE == "*") {
        num1 = Math.floor(Math.random() * (vm.getRandomRange() / 3));
        num2 = Math.floor(Math.random() * (vm.getRandomRange() / 3));
        
        questionPR = num1+" X "+num2
        answerPR = num1 * num2
      }else if (mathE == "/") {
        do {
          num1 = Math.floor(Math.random() * (vm.getRandomRange() * 2));
          num2 = Math.floor(Math.random() * (vm.getRandomRange() / 2));
        } while (num1 < num2 || num2 == 0 || num1%num2!=0);
        
        questionPR = num1+" / "+num2
        answerPR = num1 / num2
      }

      vm.question = questionPR
      vm.answer = answerPR
    },

    getRandomRange(){
      var range = 30;
      if(this.level == 0)range = 40;
      else if(this.level == 1)range = 80;
      else if(this.level == 2)range = 120;

      return range;
    },


    getTimeColor(){
      var vm = this;
      if(vm.timePassed == vm.timeLimit)return 'text-red-500'
      else if(vm.timePassed >= (vm.timeLimit/2))return 'text-yellow-500'
      else return 'text-green-500'
    },

    startTimer(){
      this.timerInterval = setInterval((() => this.timePassed += 1), 1000);
    },
    stopTimer(){
      clearInterval(this.timerInterval);
    },
    TimeOver(){
      if(this.stopGame == false){
        this.stopGame = true;
        this.stopTimer();
        this.alertTimeOver();
        console.log("Time Over")
      }
    },

    
    alertShow(msg,color = "bg-green-500"){
      var vm = this;
      vm.alert.status = true;
      vm.alert.class = "opacity-1"
      vm.alert.color = color;
      vm.alert.msg = msg;

      vm.alertFadeOut(500);
    },
    alertTimeOver(){
      var vm = this;
      vm.alert.status = true;
      vm.alert.class = "opacity-1"
      vm.alert.color = "bg-red-500";
      vm.alert.msg = "Time Over :(";

      vm.alertFadeOut(7000);
    },
    alertFadeOut(sec){
      clearTimeout(this.alerttimeout1)
      clearTimeout(this.alerttimeout2)
      var vm = this;
      vm.alerttimeout1 = setTimeout(function(){
        vm.alert.class = "opacity-0"
        vm.alerttimeout2 = setTimeout(function() {
          vm.alert.status = false;
        }, 500);
      },sec);
    },
  

    changeSettings(){
      var vm = this;
      var level = vm.level;
      vm.stopTimer();
      Object.assign(vm.$data,vm.$options.data.call(vm))
      vm.level = level;
      vm.MStart();
    }
  },
  computed: {
    timer(){
      var timeLeft = this.timeLimit - this.timePassed
      if(!timeLeft)this.TimeOver();
      return timeLeft
    }
  },
  watch: {
    level(){
      this.changeSettings();
    }
  },
}
</script>

<style>
.alertFade{
  -webkit-transition: opacity 0.5s ease-in-out;
  -moz-transition: opacity 0.5s ease-in-out;
  -ms-transition: opacity 0.5s ease-in-out;
  -o-transition: opacity 0.5s ease-in-out;
}
</style>
